import isEmpty from 'lodash/isEmpty';
import cloneDeep from 'lodash/cloneDeep';
import ConversationBase from './conversation-base';
import ConversationMemberInfo, { ConversationMemberRole } from '../conversation-member-info';
import { decodeDate, getTime, encode, keyRemap, union, difference, internal, setValue, ensureArray } from '../utils';
import {
  GenericCommand,
  ConvCommand,
  ConvMemberInfo,
  JsonObjectMessage,
  BlacklistCommand,
  OpType,
} from '../../proto/message';
import runSignatureFactory from '../signature-factory-runner';
import { createError } from '../error';

/**
 * 
 * @typedef OperationFailureError
 * @type {Error}
 * @property {string} message 
 * @property {string[]} clientIds  client id 
 * @property {number} [code] 
 * @property {string} [detail] 
 */

/**
 * 
 * @typedef PartiallySuccess
 * @type {Object}
 * @property {string[]} successfulClientIds  client id 
 * @property {OperationFailureError[]} failures 
 */

/**
 * 
 * @typedef PagedResults
 * @type {Object}
 * @property {T[]} results 
 * @property {string} [next] 
 */

const createPartiallySuccess = ({
  allowedPids,
  failedPids,
}) => ({
  successfulClientIds: allowedPids,
  failures: failedPids.map(({
    pids, ...error
  }) => Object.assign(createError(error), { clientIds: pids })),
});

/**
 * @extends ConversationBase
 * @private
 * @abstract
 */
class PersistentConversation extends ConversationBase {
  constructor(data, {
    creator,
    createdAt,
    updatedAt,
    transient = false,
    system = false,
    muted = false,
    mutedMembers = [],
    ...attributes
  }, client) {
    super({
      ...data,
      /**
       * 
       * @memberof PersistentConversation#
       * @type {String}
       */
      creator,
      /**
       * 
       * @memberof PersistentConversation#
       * @type {Date}
       */
      createdAt,
      /**
       * 
       * @memberof PersistentConversation#
       * @type {Date}
       */
      updatedAt,
      /**
       * 
       * @memberof PersistentConversation#
       * @type {?String[]}
       */
      mutedMembers,
      /**
       * 
       * @memberof PersistentConversation#
       * @type {Boolean}
       */
      transient,
      /**
       * 
       * @memberof PersistentConversation#
       * @type {Boolean}
       * @since 3.3.0
       */
      system,
      /**
       * 
       * @memberof PersistentConversation#
       * @type {Boolean}
       */
      muted,
      _attributes: attributes,
    }, client);
    this._reset();
  }

  set createdAt(value) {
    this._createdAt = decodeDate(value);
  }
  get createdAt() {
    return this._createdAt;
  }
  set updatedAt(value) {
    this._updatedAt = decodeDate(value);
  }
  get updatedAt() {
    return this._updatedAt;
  }


  /**
   *  _Conversation  name
   * @type {String}
   */
  get name() {
    return this.get('name');
  }
  set name(value) {
    this.set('name', value);
  }

  /**
   * 
   * @since 3.2.0
   * @param  {String} key key 'x'  Conversation  x 
   * @return {Any} 
   */
  get(key) {
    return internal(this).currentAttributes[key];
  }

  /**
   * 
   * @since 3.2.0
   * @param {String} key 'x'  Conversation  x  'x.y.z' 
   * @param {Any} value 
   * @return {this} self
   * @example
   *
   * //  color 
   * conversation.set('color', {
   *   text: '#000',
   *   background: '#DDD',
   * });
   * //  color.text 
   * conversation.set('color.text', '#333');
   */
  set(key, value) {
    this._debug(`set [${key}]: ${value}`);
    const { pendingAttributes } = internal(this);
    const pendingKeys = Object.keys(pendingAttributes);
    // suppose pendingAttributes = { 'a.b': {} }
    // set 'a' or 'a.b': delete 'a.b'
    const re = new RegExp(`^${key}`);
    const childKeys = pendingKeys.filter(re.test.bind(re));
    childKeys.forEach((k) => {
      delete pendingAttributes[k];
    });
    if (childKeys.length) {
      pendingAttributes[key] = value;
    } else {
      // set 'a.c': nothing to do
      // set 'a.b.c.d': assign c: { d: {} } to 'a.b'
      // CAUTION: non-standard API, provided by core-js
      const parentKey = Array.find(pendingKeys, k => key.indexOf(k) === 0); // 'a.b'
      if (parentKey) {
        setValue(pendingAttributes[parentKey], key.slice(parentKey.length + 1), value);
      } else {
        pendingAttributes[key] = value;
      }
    }
    this._buildCurrentAttributes();
    return this;
  }

  _buildCurrentAttributes() {
    const { pendingAttributes } = internal(this);
    internal(this).currentAttributes = Object.keys(pendingAttributes)
      .reduce(
        (target, k) => setValue(target, k, pendingAttributes[k]),
        cloneDeep(this._attributes),
      );
  }

  _updateServerAttributes(attributes) {
    Object.keys(attributes).forEach(key => setValue(this._attributes, key, attributes[key]));
    this._buildCurrentAttributes();
  }

  _reset() {
    internal(this).pendingAttributes = {};
    internal(this).currentAttributes = this._attributes;
  }

  /**
   * 
   * @return {Promise.<this>} self
   */
  async save() {
    this._debug('save');
    const attr = internal(this).pendingAttributes;
    if (isEmpty(attr)) {
      this._debug('nothing touched, resolve with self');
      return this;
    }
    this._debug('attr: %O', attr);
    const convMessage = new ConvCommand({
      attr: new JsonObjectMessage({
        data: JSON.stringify(encode(attr)),
      }),
    });
    const resCommand = await this._send(new GenericCommand({
      op: 'update',
      convMessage,
    }));
    this.updatedAt = resCommand.convMessage.udate;
    this._attributes = internal(this).currentAttributes;
    internal(this).pendingAttributes = {};
    return this;
  }

  /**
   * 
   * @return {Promise.<this>} self
   */
  async fetch() {
    const query = this._client.getQuery().equalTo('objectId', this.id);
    await query.find();
    return this;
  }

  /**
   * 
   * @return {Promise.<this>} self
   */
  async mute() {
    this._debug('mute');
    await this._send(new GenericCommand({
      op: 'mute',
    }));
    if (!this.transient) {
      this.muted = true;
      this.mutedMembers = union(this.mutedMembers, [this._client.id]);
    }
    return this;
  }

  /**
   * 
   * @return {Promise.<this>} self
   */
  async unmute() {
    this._debug('unmute');
    await this._send(new GenericCommand({
      op: 'unmute',
    }));
    if (!this.transient) {
      this.muted = false;
      this.mutedMembers = difference(this.mutedMembers, [this._client.id]);
    }
    return this;
  }

  async _appendConversationSignature(command, action, clientIds) {
    if (this._client.options.conversationSignatureFactory) {
      const params = [this.id, this._client.id, clientIds.sort(), action];
      const signatureResult = await runSignatureFactory(
        this._client.options.conversationSignatureFactory,
        params,
      );
      Object.assign(command.convMessage, keyRemap({
        signature: 's',
        timestamp: 't',
        nonce: 'n',
      }, signatureResult));
    }
  }

  async _appendBlacklistSignature(command, action, clientIds) {
    if (this._client.options.blacklistSignatureFactory) {
      const params = [this._client.id, this.id, clientIds.sort(), action];
      const signatureResult = await runSignatureFactory(
        this._client.options.blacklistSignatureFactory,
        params,
      );
      Object.assign(command.blacklistMessage, keyRemap({
        signature: 's',
        timestamp: 't',
        nonce: 'n',
      }, signatureResult));
    }
  }

  /**
   * 
   * @param {String|String[]} clientIds  client id
   * @return {Promise.<PartiallySuccess>}  id  id 
   */
  async add(clientIds) {
    this._debug('add', clientIds);
    if (typeof clientIds === 'string') {
      clientIds = [clientIds]; // eslint-disable-line no-param-reassign
    }
    const command = new GenericCommand({
      op: 'add',
      convMessage: new ConvCommand({
        m: clientIds,
      }),
    });
    await this._appendConversationSignature(command, 'add', clientIds);
    const {
      convMessage,
      convMessage: {
        allowedPids,
      },
    } = await this._send(command);
    if (!this.transient && !this.system) {
      this.members = union(this.members, allowedPids);
    }
    return createPartiallySuccess(convMessage);
  }

  /**
   * 
   * @param {String|String[]} clientIds  client id
   * @return {Promise.<PartiallySuccess>}  id  id 
   */
  async remove(clientIds) {
    this._debug('remove', clientIds);
    if (typeof clientIds === 'string') {
      clientIds = [clientIds]; // eslint-disable-line no-param-reassign
    }
    const command = new GenericCommand({
      op: 'remove',
      convMessage: new ConvCommand({
        m: clientIds,
      }),
    });
    await this._appendConversationSignature(command, 'remove', clientIds);
    const {
      convMessage,
      convMessage: {
        allowedPids,
      },
    } = await this._send(command);
    if (!this.transient && !this.system) {
      this.members = difference(this.members, allowedPids);
    }
    return createPartiallySuccess(convMessage);
  }

  /**
   * 
   * @return {Promise.<this>} self
   */
  async join() {
    this._debug('join');
    return this.add(this._client.id).then(({
      failures,
    }) => {
      if (failures[0]) throw failures[0];
      return this;
    });
  }

  /**
   * 
   * @return {Promise.<this>} self
   */
  async quit() {
    this._debug('quit');
    return this.remove(this._client.id).then(({
      failures,
    }) => {
      if (failures[0]) throw failures[0];
      return this;
    });
  }

  /**
   * 
   * @param {String|String[]} clientIds  client id
   * @return {Promise.<PartiallySuccess>}  id  id 
   */
  async muteMembers(clientIds) {
    this._debug('mute', clientIds);
    clientIds = ensureArray(clientIds); // eslint-disable-line no-param-reassign
    const command = new GenericCommand({
      op: OpType.add_shutup,
      convMessage: new ConvCommand({
        m: clientIds,
      }),
    });
    const {
      convMessage,
    } = await this._send(command);
    return createPartiallySuccess(convMessage);
  }

  /**
   * 
   * @param {String|String[]} clientIds  client id
   * @return {Promise.<PartiallySuccess>}  id  id 
   */
  async unmuteMembers(clientIds) {
    this._debug('unmute', clientIds);
    clientIds = ensureArray(clientIds); // eslint-disable-line no-param-reassign
    const command = new GenericCommand({
      op: OpType.remove_shutup,
      convMessage: new ConvCommand({
        m: clientIds,
      }),
    });
    const {
      convMessage,
    } = await this._send(command);
    return createPartiallySuccess(convMessage);
  }

  /**
   * 
   * @param {Object} [options]
   * @param {Number} [options.limit]  10
   * @param {String} [options.next]  next  limit 
   * @return {PagedResults.<string>}  cureser 
   */
  async queryMutedMembers({
    limit,
    next,
  } = {}) {
    this._debug('query muted: limit %O, next: %O', limit, next);
    const command = new GenericCommand({
      op: OpType.query_shutup,
      convMessage: new ConvCommand({
        limit,
        next,
      }),
    });
    const {
      convMessage: {
        m,
        next: newNext,
      },
    } = await this._send(command);
    return {
      results: m,
      next: newNext,
    };
  }

  /**
   * 
   * @param {String|String[]} clientIds  client id
   * @return {Promise.<PartiallySuccess>}  id  id 
   */
  async blockMembers(clientIds) {
    this._debug('block', clientIds);
    clientIds = ensureArray(clientIds); // eslint-disable-line no-param-reassign
    const command = new GenericCommand({
      cmd: 'blacklist',
      op: OpType.block,
      blacklistMessage: new BlacklistCommand({
        srcCid: this.id,
        toPids: clientIds,
      }),
    });
    await this._appendBlacklistSignature(command, 'conversation-block-clients', clientIds);
    const {
      blacklistMessage,
    } = await this._send(command);
    return createPartiallySuccess(blacklistMessage);
  }

  /**
   * 
   * @param {String|String[]} clientIds  client id
   * @return {Promise.<PartiallySuccess>}  id  id 
   */
  async unblockMembers(clientIds) {
    this._debug('unblock', clientIds);
    clientIds = ensureArray(clientIds); // eslint-disable-line no-param-reassign
    const command = new GenericCommand({
      cmd: 'blacklist',
      op: OpType.unblock,
      blacklistMessage: new BlacklistCommand({
        srcCid: this.id,
        toPids: clientIds,
      }),
    });
    await this._appendBlacklistSignature(command, 'conversation-unblock-clients', clientIds);
    const {
      blacklistMessage,
    } = await this._send(command);
    return createPartiallySuccess(blacklistMessage);
  }

  /**
   * 
   * @param {Object} [options]
   * @param {Number} [options.limit]  10
   * @param {String} [options.next]  next  limit 
   * @return {PagedResults.<string>}  cureser 
   */
  async queryBlockedMembers({
    limit,
    next,
  } = {}) {
    this._debug('query blocked: limit %O, next: %O', limit, next);
    const command = new GenericCommand({
      cmd: 'blacklist',
      op: OpType.query,
      blacklistMessage: new BlacklistCommand({
        srcCid: this.id,
        limit,
        next,
      }),
    });
    const {
      blacklistMessage: {
        blockedPids,
        next: newNext,
      },
    } = await this._send(command);
    return {
      results: blockedPids,
      next: newNext,
    };
  }

  /**
   * 
   * @since 4.0.0
   * @return {Promise.<ConversationMemberInfo[]>} 
   */
  async getAllMemberInfo() {
    const response = await this._client._requestWithSessionToken({
      method: 'GET',
      path: '/classes/_ConversationMemberInfo',
      query: {
        where: { cid: this.id },
      },
    });
    const memberInfos = response.results.map(info => new ConversationMemberInfo({
      conversation: this,
      memberId: info.clientId,
      role: info.role,
    }));
    const memberInfoMap = {};
    memberInfos.forEach((memberInfo) => {
      memberInfoMap[memberInfo.memberId] = memberInfo;
    });
    this.members.forEach((memberId) => {
      memberInfoMap[memberId] = new ConversationMemberInfo({
        conversation: this,
        memberId,
        role: ConversationMemberRole.MEMBER,
      });
    });
    internal(this).memberInfoMap = memberInfoMap;
    return this.members.map(memberId => memberInfoMap[memberId]);
  }

  /**
   * 
   * @since 4.0.0
   * @return {Promise.<ConversationMemberInfo>} 
   */
  async getMemberInfo(memberId) {
    if (this.members.indexOf(memberId) === -1) throw new Error(`${memberId} is not the mumber of conversation[${this.id}]`);
    const { memberInfoMap } = internal(this);
    if (!(memberInfoMap && memberInfoMap[memberId])) await this.getAllMemberInfo();
    return internal(this).memberInfoMap[memberId];
  }

  /**
   * 
   * @since 4.0.0
   * @param {String} memberId  Id
   * @param {module:leancloud-realtime.ConversationMemberRole | String} role 
   * @return {Promise.<this>} self
   */
  async updateMemberRole(memberId, role) {
    this._debug('mute');
    await this._send(new GenericCommand({
      op: OpType.member_info_update,
      convMessage: new ConvCommand({
        targetClientId: memberId,
        info: new ConvMemberInfo({
          pid: memberId,
          role,
        }),
      }),
    }));
    const { memberInfos } = internal(this);
    if (memberInfos && memberInfos[memberId]) {
      internal(memberInfos[memberId]).role = role;
    }
    return this;
  }

  toFullJSON() {
    const {
      creator, system, transient,
      createdAt, updatedAt,
      _attributes,
    } = this;
    return {
      ...super.toFullJSON(),
      creator,
      system,
      transient,
      createdAt: getTime(createdAt),
      updatedAt: getTime(updatedAt),
      ..._attributes,
    };
  }

  toJSON() {
    const {
      creator, system, transient, muted, mutedMembers,
      createdAt, updatedAt,
      _attributes,
    } = this;
    return {
      ...super.toJSON(),
      creator,
      system,
      transient,
      muted,
      mutedMembers,
      createdAt,
      updatedAt,
      ..._attributes,
    };
  }
}

export default PersistentConversation;
